import logo from '../../assets/logo.png'
export default {
  data () {
    return {
      menus: [{
        name: '基本信息',
        img: logo,
        tip: '酒吧名称',
        route: {
          path: '/BasicBusiness'
        }
      }, {
        name: '酒吧公告',
        img: '',
        tip: '公告编辑',
        route: {
          path: '/NoticeSetting'
        }
      }, {
        name: '霸屏管理',
        img: '',
        tip: '价格、类型、时长等',
        route: {
          path: '/BaPingSetting'
        }
      }, {
        name: '背景设置',
        img: '',
        tip: '大屏幕背景',
        route: {
          path: '/BgSetting'
        }
      }, {
        name: '广告设置',
        img: '',
        tip: '广告图片设置',
        route: {
          path: '/AdSetting'
        }
      }, {
        name: '管理员',
        img: '',
        tip: '管理员编辑',
        route: {
          path: '/Manager'
        }
      }, {
        name: '帮助说明',
        img: '',
        tip: '设置帮助',
        route: {
          path: '/Help'
        }
      }]
    }
  },
  components: {
  },
  methods: {
    route (index) {
      this.$router.push(this.menus[index].route)
    }
  }
}



